import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)

const router =  new Router({
  routes: [
    {
      path: '/wx/paySelect',
      name: 'paySelect',
      component: () => import('@/modules/wx/paySelect')
    },
    {
      path: '/paySuccess',
      name: 'paySuccess',
      component: () => import('@/modules/paySuccess')
    },
    {
      path: '/payError',
      name: 'payError',
      component: () => import('@/modules/payError')
    },
    {
      path: '/zfb/paySelect',
      name: 'zfb',
      component: () => import('@/modules/zfb/paySelect')
    },
    {
      path: '/jd/paySelect',
      name: 'jd',
      component: () => import('@/modules/jd/paySelect')
    },
  ]
})

export default router



